//
// Segmented controllers
// --------------------------------------------------

.segmented-control {
  position: relative;
  display: table;
  overflow: hidden;
  font-size: 14px;
  font-weight: @font-weight-light;
  background-color: @chrome-color;
  border: 1px solid #ccc;
  border-radius: 3px;

  // Section within controller
  .control-item {
    display: table-cell;
    width: 1%;
    padding-top: 6px;
    padding-bottom: 7px;
    overflow: hidden;
    line-height: 1;
    color: #333;
    text-align: center;
    text-overflow: ellipsis;
    white-space: nowrap;
    border-left: 1px solid #ccc;

    // Remove border-left and shadow from first section
    &:first-child {
      border-left-width: 0;
    }

    // Tap state of segmented controller
    &:active {
      background-color: #eee;
    }

    // Selected state of segmented controller
    &.active {
      background-color: #ccc;
    }
  }

  &.segmented-control-inverted
  {
    width: 100%;
    border: 0;
    border-radius: 0;

    .control-item
    {
      color: inherit;
      border: 0;
      padding-top: 10px;
      border-bottom: 3px solid rgba(0,0,0,0.075);

      &.active
      {
        color: @primary-color;
        background: none;
        border-color: @primary-color;
      }
    }
  }
}

// Other segmented controller types
// --------------------------------------------------

// Primary
.segmented-control-primary {
  border-color: @primary-color;

  .control-item {
    color: @primary-color;
    border-color: inherit;

    &:active {
      background-color: lighten(@primary-color, 35%);
    }
    &.active {
      color: #fff;
      background-color: @primary-color;
    }
  }
}

// Positive
.segmented-control-positive {
  border-color: @positive-color;

  .control-item {
    color: @positive-color;
    border-color: inherit;

    &:active {
      background-color: lighten(@positive-color, 35%);
    }
    &.active {
      color: #fff;
      background-color: @positive-color;
    }
  }
}

// Negative
.segmented-control-negative {
  border-color: @negative-color;

  .control-item {
    color: @negative-color;
    border-color: inherit;

    &:active {
      background-color: lighten(@negative-color, 35%);
    }
    &.active {
      color: #fff;
      background-color: @negative-color;
    }
  }
}

// This is used to by the js to show and hide content tide to the segmented control.
.control-content {
  display: none;

  &.active {
    display: block;
  }
}
