//
// Variables
// --------------------------------------------------


// Type
// --------------------------------------------------
@font-family-default : "Helvetica Neue", Helvetica, sans-serif;
@font-size-default   : 17px;
@font-weight         : 500;
@font-weight-light   : 400;
@line-height-default : 21px;


// Colors
// --------------------------------------------------

// Main theme colors
// @primary-color: #428bca; // old color
@primary-color : #007bc6;
@chrome-color  :  #fff;

// Action colors
@default-color  :  #fff;
@positive-color : #5cb85c;
@negative-color : #d9534f;
@warning-color  : #ed9922;
@purple-color   : #8A6DE9;


// Bars
// --------------------------------------------------

@bar-base-height:  44px;
@bar-tab-height:   50px;
@bar-side-spacing: 10px;


// Cards
// --------------------------------------------------

@card-bg: #fff;


// Buttons
// --------------------------------------------------

@button-font-size: 12px;


// Transitions
// --------------------------------------------------

@timing-fuction: cubic-bezier(.1,.5,.1,1); // Inspired by @c2prods


// Borders
// --------------------------------------------------

@border-default: 1px solid #ddd;
@border-radius: 6px;
